<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar">
			<el-form :inline="true" :model="filters">
				<el-form-item label="">
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="filters.code" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUsers"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
	
		<!---->
		<template>
			<el-table :data="users" highlight-current-row v-loading="listLoading" style="width: 100%;">
				<el-table-column type="index" width="60">
				</el-table-column>
				<el-table-column prop="code" label="" width="120" sortable>
				</el-table-column>
				<el-table-column prop="name" label="" width="120" sortable>
				</el-table-column>
				<el-table-column prop="address" label="" width="120" sortable>
				</el-table-column>
				<el-table-column prop="phone" label="" width="180" sortable>
				</el-table-column>
				<el-table-column prop="link_name" label="" width="180" sortable>
				</el-table-column>
				<el-table-column prop="link_tel" label="" width="180" sortable>
				</el-table-column>
				<el-table-column inline-template :context="_self" label="" min-width="320">
					<span>
						<el-button size="small" @click="handleEdit(row)"></el-button>
						<!--<el-button type="danger" size="small" @click="handleDel(row)"></el-button>-->
					</span>
				</el-table-column>
			</el-table>
		</template>
	
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom:10px;">
			<el-pagination layout="total,sizes,prev, pager, next" @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-sizes="[10, 200, 300, 400]" :page-size="pagesize" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	
		<!---->
		<el-dialog :title="editFormTtile" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form ref="form" :model="form" label-width="100px" :rules="editFormRules" @submit.prevent="onSubmit" >
				<el-form-item label="" prop="name">
					<el-input v-model="form.name"></el-input>
				</el-form-item>
				<el-form-item label="" prop="link_name">
					<el-input v-model="form.link_name"></el-input>
				</el-form-item>
				<el-form-item label="" prop="link_tel">
					<el-input v-model="form.link_tel"></el-input>
				</el-form-item>
				<el-form-item label="" prop="wechat">
					<el-input v-model="form.wechat"></el-input>
				</el-form-item>
				<el-form-item label="" prop="emali">
					<el-input v-model="form.emali"></el-input>
				</el-form-item>
				<el-form-item label="QQ" prop="qq">
					<el-input v-model="form.qq"></el-input>
				</el-form-item>
				<el-form-item label="" prop="phone">
					<el-input v-model="form.phone"></el-input>
				</el-form-item>
	
				<el-form-item label="" prop="province">
					<el-cascader size="large" :options="options" v-model="selectedOptions" @change="handleaddresschange">
					</el-cascader>
				</el-form-item>
				<el-form-item label="" prop="address">
					<el-input v-model="form.address"></el-input>
				</el-form-item>
				<el-form-item label="" prop="note">
					<el-input type="textarea" v-model="form.note"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button :loading="editLoading" type="primary" @click="saveShop">{{btnEditText}}</el-button>
					<el-button @click.native.prevent="returnShop"></el-button>
				</el-form-item>
			</el-form>
		</el-dialog>
	</section>
</template>

<script>
import util from '../../common/util'
import NProgress from 'nprogress'
import request, { getUserListPage, removeUser, editUser, addUser } from 'api';
import config from 'config';
import { provinceAndCityData, regionData, provinceAndCityDataPlus, regionDataPlus, CodeToText, TextToCode } from 'element-china-area-data';
export default {
	data() {
		return {
			filters: {
				name: '',
				code: '',
				page: 1
			},
			options: regionData,
			selectedOptions: [],
			users: [],
			total: 0,
			page: 1,
			pagesize: 10,
			listLoading: false,
			editFormVisible: false,//
			editFormTtile: '',//
			shopdetailVisible: false,
			shopdetailtitle: '',
			shopdetailobj: {

			},
			//
			editForm: {
				id: 0,
				name: '',
				sex: -1,
				age: 0,
				birth: '',
				addr: ''
			},
			form: {
				id: 0,
				name: '',
				link_name: '',
				phone: '',
				link_tel: '',
				province: '',
				city: '',
				area: '',
				address: '',
				wechat: '',
				qq: '',
				emali: '',
				note:""
			},
			editLoading: false,
			btnEditText: ' ',
			editFormRules: {
				name: [
					{ required: true, message: '', trigger: 'blur' }
				],
				link_name: [
					{ required: true, message: '', trigger: 'blur' }
				],
				phone: [
					{ required: true, message: '', trigger: 'blur' },
				],
				link_tel: [
					{ required: true, message: '', trigger: 'blur' }
				],
				province: [
					{ required: true, message: '', trigger: 'blur' }
				],
				city: [
					{ required: true, message: '', trigger: 'blur' }
				],
				area: [
					{ required: true, message: '', trigger: 'blur' }
				],
				address: [
					{ required: true, message: '', trigger: 'blur' }
				],
				wechat: [
					{ required: true, message: '', trigger: 'blur' }
				],
				qq: [
					{ required: true, message: 'QQ', trigger: 'blur' }
				],
				emali: [
					{ required: true, message: '', trigger: 'blur' },
					{ type: 'email', message: '', trigger: 'blur,change' }
				],
				note: [
					{ required: true, message: '', trigger: 'blur' }
				]
			}

		}
	},
	methods: {
		//
		formatSex: function (row, column) {
			return row.sex == 1 ? '' : row.sex == 0 ? '' : '';
		},
		handleCurrentChange(val) {
			this.page = val;
			this.filters.page = val;
			this.getUsers();
		},
		handleSizeChange(val) {
			console.log(` ${val} `);
		},
		handleaddresschange(value) {

			this.form.province = CodeToText[value[0]]
			this.form.city = CodeToText[value[1]]
			this.form.area = CodeToText[value[value.length - 1]]
		},
		//
		getUsers() {

			let para = {
				page: this.page,
				name: this.filters.name
			};
			this.listLoading = true;
			NProgress.start();
			// getUserListPage(para).then((res) => {
			// 	this.total = res.data.total;
			// 	this.users = res.data.users;
			// 	this.listLoading = false;
			// 	NProgress.done();
			// });
			request.get(config.api.goods.supplierindex, this.filters)
				.then((res) => {
					this.listLoading = false;
					NProgress.done();
					let { message, code, data } = res;
					if (code !== 200) {
						this.$notify({
							title: '',
							message: message,
							type: 'error'
						});
					} else {
						this.total = data.cnt.total;
						this.users = data.cnt.data;
						this.pagesize = data.cnt.per_page || 10;
					}
				})
				.catch(err=>{
					this.listLoading = false;
				})
		},
		//
		handleDel: function (row) {
			//console.log(row);
			var _this = this;
			this.$confirm('?', '', {
				//type: 'warning'
			}).then(() => {
				_this.listLoading = true;
				NProgress.start();
				let para = { id: row.id };
				removeUser(para).then((res) => {
					_this.listLoading = false;
					NProgress.done();
					_this.$notify({
						title: '',
						message: '',
						type: 'success'
					});
					_this.getUsers();
				});

			}).catch(() => {

			});
		},
		showshopdetail(row) {
			this.shopdetailVisible = true;
			request.get(config.api.shop.shopqueuedetail, { shop_id: row.id })
				.then((res) => {
					let { message, code, data } = res;
					if (code !== 200) {
						this.$notify({
							title: '',
							message: message,
							type: 'error'
						});
					} else {
						this.shopdetailobj = data.cnt;
					}
				})
		},
		//
		showorder(row) {
			this.$router.push('/shoporder');
		},
		//
		handleEdit: function (row) {
			this.editFormVisible = true;
			this.editFormTtile = '';
			for(let key in this.form){
				this.form[key]=row[key]
			}
			this.selectedOptions=[];

			this.selectedOptions=[TextToCode[row.province].code,TextToCode[row.province][row.city].code,TextToCode[row.province][row.city][row.area].code];
			
			// this.editForm.id = row.id;
			// this.editForm.name = row.name;
			// this.editForm.sex = row.sex;
			// this.editForm.age = row.age;
			// this.editForm.birth = row.birth;
			// this.editForm.addr = row.addr;

			// this.$router.push('/addsupplier/' + row.id);

		},
		saveShop() {
			var _this = this;

			_this.$refs.form.validate((valid) => {
				if (valid) {

					_this.$confirm('', '', {}).then(() => {
						_this.editLoading = true;
						NProgress.start();
						_this.btnEditText = '';

						if (_this.form.id == 0) {

							//
							let para = _this.form;
							delete para.id;

							request.post(config.api.goods.addsupplier, para)
								.then(res => {
									let { message, code, data } = res;
									_this.editLoading = false;
									NProgress.done();
									_this.btnEditText = ' ';
									if (code !== 200) {
										this.$notify({
											title: '',
											message: message,
											type: 'error'
										});
									} else {
										_this.$notify({
											title: '',
											message: '',
											type: 'success'
										});
										_this.editFormVisible = false;
										_this.getUsers();
									}
								}).catch(err=>{
									_this.editLoading = false;
								})
						} else {
							//
							
							request.post(config.api.goods.updatesupplier, _this.form)
								.then(res => {
									let { message, code, data } = res;
									_this.editLoading = false;
									NProgress.done();
									_this.btnEditText = ' ';
									if (code !== 200) {
										this.$notify({
											title: '',
											message: message,
											type: 'error'
										});
									} else {
										_this.$notify({
											title: '',
											message: '',
											type: 'success'
										});
										_this.editFormVisible = false;
										_this.getUsers();
									}
								}).catch(err=>{
									_this.editLoading = false;
								})

						}

					});

				}
			});
			// this.$router.replace('/shop')
		},
		// or 
		editSubmit: function () {
			var _this = this;

			_this.$refs.editForm.validate((valid) => {
				if (valid) {

					_this.$confirm('', '', {}).then(() => {
						_this.editLoading = true;
						NProgress.start();
						_this.btnEditText = '';

						if (_this.editForm.id == 0) {
							//
							let para = {
								name: _this.editForm.name,
								sex: _this.editForm.sex,
								age: _this.editForm.age,
								birth: _this.editForm.birth == '' ? '' : util.formatDate.format(new Date(_this.editForm.birth), 'yyyy-MM-dd'),
								addr: _this.editForm.addr,
							};
							addUser(para).then((res) => {
								_this.editLoading = false;
								NProgress.done();
								_this.btnEditText = ' ';
								_this.$notify({
									title: '',
									message: '',
									type: 'success'
								});
								_this.editFormVisible = false;
								_this.getUsers();
							});
						} else {
							//
							let para = {
								id: _this.editForm.id,
								name: _this.editForm.name,
								sex: _this.editForm.sex,
								age: _this.editForm.age,
								birth: _this.editForm.birth == '' ? '' : util.formatDate.format(new Date(_this.editForm.birth), 'yyyy-MM-dd'),
								addr: _this.editForm.addr,
							};
							editUser(para).then((res) => {
								_this.editLoading = false;
								NProgress.done();
								_this.btnEditText = ' ';
								_this.$notify({
									title: '',
									message: '',
									type: 'success'
								});
								_this.editFormVisible = false;
								_this.getUsers();
							});

						}

					});

				}
			});

		},
		//
		handleAdd: function () {
			var _this = this;

			this.editFormVisible = true;
			this.editFormTtile = '';
			for(let key in this.form){
				this.form[key]='';
			}
			this.form.id = 0;
			// this.$router.push('/addsupplier/0');
		}
	},
	mounted() {
		this.getUsers();
	}
}
</script>

<style scoped>
.el-card__header {
	background-color: #E4E4E4
}

.clearfix:before,
.clearfix:after {
	display: table;
	content: "";
}

.clearfix:after {
	clear: both
}
</style>


