<template>
    <section>
        <el-col :span="24" class="toolbar">
            <el-tabs v-model="activeName" type="card" @tab-click="handleClick" style="padding-bottom:20px;">
                <el-popover ref="popoverF" placement="right" width="400" trigger="click">
                    <el-upload action="" :file-list="filelist" :http-request="handleRequestOss" list-type="picture" :on-change="handlechange" :on-preview="handlePictureCardPreview" :on-remove="handleRemove" :on-success="getUpstr">
                        <el-button size="small" type="primary"></el-button>
                    </el-upload>
                    <el-dialog v-model="dialogVisible" size="tiny">
                        <img width="100%" :src="dialogImageUrl" alt="">
                    </el-dialog>
                    <!--<el-button @click="addtitle" style="margin:20px;"></el-button>-->
                </el-popover>
                <el-popover ref="popoverT" placement="right" width="400" trigger="click">
                    <el-upload action="" :file-list="filelist1" :http-request="handleRequestOssBanner" list-type="picture-card" :on-remove="handleRemoveBanner" :on-success="getUpstr">
                        <i class="el-icon-plus"></i>
                    </el-upload>
                    <el-dialog v-model="dialogVisible" size="tiny">
                        <img width="100%" :src="dialogImageUrl" alt="">
                    </el-dialog>
                </el-popover>
                <el-tab-pane label="" name="first">
                    <el-form :model="form" label-width="80px" :rules="editFormRules" ref="form">
                        <el-card class="box-card" style="margin-bottom:20px">
                            <div slot="header" class="clearfix">
                                <span style="line-height: 20px;"></span>
                            </div>
                            <el-form-item label="" prop="name" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
                                <el-input v-model="form.name" auto-complete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" prop="code" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
                                <el-input v-model="form.code" auto-complete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" prop="production_origin" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
                                <el-input v-model="form.production_origin" auto-complete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" prop="manufacturer" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
                                <el-input v-model="form.manufacturer" auto-complete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
                                <el-input v-model="form.num" auto-complete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
                                <el-select v-model="form.valuation_id" placeholder="">
                                    <el-option label="" :value="1"></el-option>
                                    <el-option label="" :value="0"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
                                <el-select v-model="form.retail_unit_id" placeholder="">
                                    <el-option label="" :value="1"></el-option>
                                    <el-option label="" :value="0"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
                                <el-select v-model="form.goods_type_id" placeholder="">
                                    <el-option label="" :value="1"></el-option>
                                    <el-option label="" :value="0"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
                                <el-select v-model="form.goods_brand_id" placeholder="">
                                    <el-option label="" :value="1"></el-option>
                                    <el-option label="" :value="0"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
                                <el-select v-model="form.supplier_id" placeholder="">
                                    <el-option label="" :value="1"></el-option>
                                    <el-option label="" :value="0"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
                                <el-switch v-model="form.is_rebate" on-color="#13ce66" off-color="#ff4949" on-value="1" off-value="0">
                                </el-switch>
                            </el-form-item>
    
                        </el-card>
                        <el-card class="box-card">
                            <div slot="header" class="clearfix">
                                <span style="line-height: 20px;"></span>
                            </div>
                            <el-form-item label="" prop="buying_price" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
                                <el-input v-model="form.buying_price" auto-complete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" prop="trade_price" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
                                <el-input v-model="form.trade_price" auto-complete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" prop="retail_price" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
                                <el-input v-model="form.retail_price" auto-complete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" prop="market_price" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
                                <el-input v-model="form.market_price" auto-complete="off"></el-input>
                            </el-form-item>
                            <!--<el-form-item label="" prop="name" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
                                <el-input v-model="form.name" auto-complete="off"></el-input>
                            </el-form-item>-->
                        </el-card>
                    </el-form>
    
                </el-tab-pane>
                <el-tab-pane label="" name="second">
                    <div class="phoneBg" style="padding:18px;padding-top:32px;width:284px;height:600px;position:relative;">
                        <el-carousel indicator-position="outside" width="300px" height="200px">
                            <el-carousel-item v-for="item in pic" :key="item">
                                <img :src="item" width="100%">
                            </el-carousel-item>
                        </el-carousel>
                        <el-button style="width:284px;height:200px;overflow:hidden;position:absolute;border:none;top:32px;z-index:100;opacity:0" @click="test" v-popover:popoverT>
                        </el-button>
                        <el-button id="imageContent" style="width:100%;height:300px;overflow:auto;margin-left:-1px"  v-popover:popoverF>
                            <div v-html="editForm.content"></div>
                            <div class="img-single" style="width:100%;" v-for="item in picContent" :key="item">
                                <div class="deleteAvata" style="display: none;"></div>
                                <img src="http://enclosure.wandlm.net/web_pic/2017/01/09/1483941258379.png" :src="item" width="100%">
                            </div>
                        </el-button>
    
                    </div>
                    <el-form ref="editForm" :model="editForm" label-width="80px" :rules="editFormRules">
                        <el-form-item label="" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
                            <el-switch v-model="editForm.status" on-color="#13ce66" off-color="#ff4949" on-value="1" off-value="0">
                            </el-switch>
                        </el-form-item>
                        <el-form-item label="" style="width:300px">
                            <el-input type="area" v-model="editForm.note"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-input type="hidden" v-model="editForm.pic"></el-input>
                            <el-input type="hidden" v-model="editForm.content"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button :loading="editLoading" type="primary" @click="editSubmit">{{btnEditText}}</el-button>
                            <el-button @click.native.prevent="returnShop"></el-button>
                        </el-form-item>
                    </el-form>
    
                </el-tab-pane>
    
                <!--<el-button v-popover:popover4>click </el-button>-->
            </el-tabs>
        </el-col>
        <!---->
        <template>
    
        </template>
    
    </section>
</template>

<script>
import util from '../../common/util'
import NProgress from 'nprogress'
import request from 'api';
import config from 'config';
import Sign from 'common/sign'
export default {
    data() {
        return {
            activeName: 'first',
            filters: {
                name: ''
            },
            //
            dialogImageUrl: '',
            dialogVisible: false,
            filelist: [],
            filelist1: [],
            visible2: false,
            users: [],
            pic: [],
            picContent: [],
            total: 0,
            page: 1,
            listLoading: false,
            editFormVisible: false,//
            editFormTtile: '',//
            //
            editForm: {
                id: 0,
                pic: '',
                content: '',
                note: '',
                status: ''
            },
            //
            form: {
                id: 0
            },
            editLoading: false,
            btnEditText: ' ',
            editFormRules: {
                name: [
                    { required: true, message: '', trigger: 'blur' }
                ]
            }

        }
    },
    methods: {
        test() {
            console.log('12144141 test')
        },
        //
        formatSex: function (row, column) {
            return row.sex == 1 ? '' : row.sex == 0 ? '' : '';
        },
        //
        adddetail() {
            console.log('1213133')
        },
        //
        addtitle() {
            this.$prompt('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                // inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
                // inputErrorMessage: ''
            }).then(({ value }) => {

            }).catch(() => {
                // this.$message({
                //     type: 'info',
                //     message: ''
                // });
            });
        },
        handleClick() {

        },
        handleCurrentChange(val) {
            this.page = val;
            this.getUsers();
        },
        //
        handleRemove(file, fileList) {

            for (let i = 0, j = this.picContent.length; i < j; i++) {
                if (this.picContent[i] == file.url) {
                    this.picContent.splice(i, 1);
                    i--;
                }
            }
        },
        handleRemoveBanner(file, fileList) {

            for (let i = 0, j = this.pic.length; i < j; i++) {
                if (this.pic[i] == file.url) {
                    this.pic.splice(i, 1);
                    i--;
                }
            }
        },
        handlePictureCardPreview(file) {
            console.log(file)
            console.log('qianzhi')
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
        },
        getUpstr() {
            console.log('chenggongshi ')
        },
        handlechange() {
            console.log('handlechange ')
        },
        handleBeforeup() {
            console.log('handleBeforeup ')
        },
        //
        getUsers() {
            
            let { params } = this.$route;
            this.editForm.id = params.id
            let para = {
                id: params.id
            };
            this.listLoading = true;
            NProgress.start();
            
            request.get(config.api.goods.goodsdetail, para)
				.then((res) => {
					this.listLoading = false;
					NProgress.done();
					let { message, code, data } = res;
					if (code !== 200) {
						this.$notify({
							title: '',
							message: message,
							type: 'error'
						});
					} else {

						this.form = data.cnt;
                        this.form.is_rebate+='';
                        this.form.pic=JSON.stringify(this.form.pic)
                        for (let key in this.editForm){
                            this.editForm[key]=this.form[key]+'';
                        }
                        this.filelist1=[]
                        this.filelist=[]
                        
                        //
                        if(this.form.pic){
                            this.pic=this.form.pic instanceof Array?this.form.pic:JSON.parse(this.form.pic);
                            this.pic=this.pic?this.pic:[];
                            this.pic.forEach(item=>{
                                this.filelist1.push({name:'img',url:item})
                            })
                        }
                        
                        //
                        // this.filelist1
					}
				})
        },
        handleRequestOssBanner(files) {
            let file = files.file
            Sign.then((client) => {
                client.multipartUpload(file.name, file)
                    .then(res => {
                        this.pic.push((res.res.requestUrls[0]).split('?')[0]);
                        this.filelist1 = [];
                        
                        this.pic.forEach((item, index) => {
                            this.filelist1.push({ name: index, url: item })
                        })
                        // this.editForm.pic = this.editForm.pic.push(res.url);
                    }).catch(err => {
                        console.log(err)
                    })
            })

        },
        handleRequestOss(files) {
            // client.list({
            //     'max-keys': 10
            // }).then(res => {
            //     console.log(res)
            // }).catch(err => {
            //     console.log(err)
            // })

            let file = files.file
            Sign.then((client) => {
                client.multipartUpload(file.name, file)
                    .then(res => {
                        this.picContent.push((res.res.requestUrls[0]).split('?')[0]);
                        this.filelist1 = [];
                        this.picContent.forEach((item, index) => {
                            this.filelist1.push({ name: index, url: item })
                        })
                    }).catch(err => {
                        console.log(err)
                    })
            })

        },
        //
        handleDel: function (row) {
            //console.log(row);
            var _this = this;
            this.$confirm('?', '', {
                //type: 'warning'
            }).then(() => {
                _this.listLoading = true;
                NProgress.start();
                let para = { id: row.id };
                removeUser(para).then((res) => {
                    _this.listLoading = false;
                    NProgress.done();
                    _this.$notify({
                        title: '',
                        message: '',
                        type: 'success'
                    });
                    _this.getUsers();
                });

            }).catch(() => {

            });
        },
        //
        handleEdit: function (row) {
            // this.editFormVisible = true;
            // this.editFormTtile = '';
            // this.editForm.id = row.id;
            // this.editForm.name = row.name;
            // this.editForm.sex = row.sex;
            // this.editForm.age = row.age;
            // this.editForm.birth = row.birth;
            // this.editForm.addr = row.addr;
            this.$router.push('/goods-detail-edit')
        },
        // or 
        editSubmit: function () {
            var _this = this;

            _this.$refs.editForm.validate((valid) => {
                if (valid) {

                    _this.$confirm('', '', {}).then(() => {
                        _this.editLoading = true;
                        NProgress.start();
                        _this.btnEditText = '';

                        //
                        let para = _this.editForm;
                        para.content = imageContent.innerHTML;
                        para.pic = JSON.stringify(_this.pic);
                        // delete para.id;
                        request.post(config.api.goods.goodsdetailadd, para)
                            .then(res => {
                                let { message, code, data } = res;
                                _this.editLoading = false;
                                NProgress.done();
                                _this.btnEditText = ' ';
                                if (code !== 200) {
                                    this.$notify({
                                        title: '',
                                        message: message,
                                        type: 'error'
                                    });
                                } else {
                                    _this.$notify({
                                        title: '',
                                        message: '',
                                        type: 'success'
                                    });
                                    _this.editFormVisible = false;
                                    _this.$router.replace('/goods-detail');
                                }
                            })

                        // if (_this.editForm.id == 0) {
                        //     //

                        // } else {
                        //     //
                        //     let para = _this.editForm;

                        //     request.post(config.api.help.updatebank, para)
                        //         .then(res => {
                        //             let { message, code, data } = res;
                        //             _this.editLoading = false;
                        //             NProgress.done();
                        //             _this.btnEditText = ' ';
                        //             if (code !== 200) {
                        //                 this.$notify({
                        //                     title: '',
                        //                     message: message,
                        //                     type: 'error'
                        //                 });
                        //             } else {
                        //                 _this.$notify({
                        //                     title: '',
                        //                     message: '',
                        //                     type: 'success'
                        //                 });
                        //                 _this.editFormVisible = false;
                        //                 _this.getUsers();
                        //             }
                        //         })
                        // }

                    });

                }
            });

        },
        //
        handleAdd: function () {
            var _this = this;

            this.editFormVisible = true;
            this.editFormTtile = '';

            this.editForm.id = 0;
            this.editForm.name = '';
            this.editForm.sex = 1;
            this.editForm.age = 0;
            this.editForm.birth = '';
            this.editForm.addr = '';
        }
    },
    mounted() {
        this.getUsers();
    }
}
</script>

<style scoped>
.el-card__header {
    background-color: #E4E4E4
}

.clearfix:before,
.clearfix:after {
    display: table;
    content: "";
}

.clearfix:after {
    clear: both
}

.phoneBg {
    background: url('../../assets/iphone.png') no-repeat 0 0;

    background-size: contain;
}

.el-carousel__item h3 {
    color: #475669;
    font-size: 18px;
    opacity: 0.75;
    line-height: 300px;
    margin: 0;
}

.el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
}
</style>


