<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<!--<el-col :span="11">
						<el-date-picker type="date" placeholder="" v-model="filters.time" style="width: 100%;"></el-date-picker>
					</el-col>-->
                    <el-date-picker v-model="filters.time" type="datetime" placeholder="" align="right" :picker-options="pickerOptions1">
				</el-date-picker>
					<!--<el-col class="line" :span="2"> - </el-col>
					<el-col :span="11">
						<el-time-picker type="fixed-time" placeholder="" v-model="filters.datee" style="width: 100%;"></el-time-picker>
					</el-col>-->
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUsers"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
	
		<!---->
		<template>
			<el-table :data="users" highlight-current-row v-loading="listLoading" style="width: 100%;">
				<el-table-column type="index" width="60">
				</el-table-column>
				<el-table-column prop="order_num" label="" width="120" sortable>
				</el-table-column>
				<el-table-column prop="money_all" label="" width="120" sortable>
				</el-table-column>
				<el-table-column prop="money" label="" width="120" sortable>
				</el-table-column>
                <el-table-column prop="money_company" label="" width="120" sortable>
				</el-table-column>
                <el-table-column prop="money_shop" label="" width="120" sortable>
				</el-table-column>
                <el-table-column prop="money_queue" label="" width="120" sortable>
				</el-table-column>
				<el-table-column prop="money_product" label="" width="180" sortable>
				</el-table-column>
				<el-table-column prop="audit_date" label="" min-width="180" sortable>
				</el-table-column>
				<!--<el-table-column inline-template :context="_self" label="" min-width="320">
					<span>
						<el-button size="small" @click="handleEdit(row)"></el-button>
					</span>
				</el-table-column>-->
			</el-table>
		</template>
	
		<!---->
        <el-col :span="24" class="toolbar" style="padding-bottom:10px;">
            <el-pagination layout="total,sizes,prev, pager, next" @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-sizes="[10, 200, 300, 400]" :page-size="pagesize" :total="total" style="float:right;">
            </el-pagination>
        </el-col>
	
		<!---->
		<el-dialog :title="editFormTtile" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<!--<el-select v-model="editForm.sex" placeholder="">
							<el-option label="" :value="1"></el-option>
							<el-option label="" :value="0"></el-option>
						</el-select>-->
					<el-radio-group v-model="editForm.sex">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="">
					<el-input-number v-model="editForm.age" :min="0" :max="200"></el-input-number>
				</el-form-item>
				<el-form-item label="">
					<el-date-picker type="date" placeholder="" v-model="editForm.birth"></el-date-picker>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" v-model="editForm.addr"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"> </el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
			</div>
		</el-dialog>
	</section>
</template>


<script>
import util from 'common/util'
import NProgress from 'nprogress'
import request, { getUserListPage, removeUser, editUser, addUser } from 'api';
import config from 'config';
import client from 'common/sign'
export default {
    data() {
        return {
            pickerOptions1: {
				shortcuts: [{
					text: '',
					onClick(picker) {
						picker.$emit('pick', new Date());
					}
				}, {
					text: '',
					onClick(picker) {
						const date = new Date();
						date.setTime(date.getTime() - 3600 * 1000 * 24);
						picker.$emit('pick', date);
					}
				}, {
					text: '',
					onClick(picker) {
						const date = new Date();
						date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
						picker.$emit('pick', date);
					}
				}]
			},
            filters: {
                time: '',
                page:1,
            },
            //
            dialogImageUrl: '',
            dialogVisible: false,
            users: [],
            total: 0,
            page: 1,
            pagesize: 10,
            listLoading: false,
            editFormVisible: false,//
            editFormTtile: '',//
            shopdetailVisible: false,
            shopdetailtitle: '',
            shopdetailobj: {

            },
            filelist: [],
            //
            editForm: {
                id: 0,
                name: '',
                logo: ''
            },
            editLoading: false,
            editstatusLoading:false,
            btnEditText: ' ',
            editFormRules: {
                name: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                logo: [
                    { required: true, message: '', trigger: 'blur' }
                ]
            }

        }
    },
    methods: {
        //
        formatSex: function (row, column) {
            return row.status == 1 ? '' : row.sex == 0 ? '' : '';
        },
        //
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePictureCardPreview(file) {
            console.log(file)
            console.log('qianzhi')
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
        },
        getUpstr() {
            console.log('chenggongshi ')
        },
        handlechange() {
            console.log('handlechange ')
        },
        handleBeforeup() {
            console.log('handleBeforeup ')
        },
        filterTag(value, row) {
            console.log(value)
            console.log(row)
            return row.status === value;
        },
        handleCurrentChange(val) {
            this.page = val;
            this.filters.page = val;
            this.getUsers();
        },
        handleSizeChange(val) {
            console.log(` ${val} `);
        },
        handleRequestOss(files) {


            // client.list({
            //     'max-keys': 10
            // }).then(res => {
            //     console.log(res)
            // }).catch(err => {
            //     console.log(err)
            // })

            let file = files.file
            client.multipartUpload(file.name, file)
                .then(res => {
                    this.editForm.logo = res.url;
                }).catch(err => {
                    console.log(err)
                })
        },
        //
        getUsers() {

            let para = this.filters;
            this.listLoading = true;
            NProgress.start();
            // getUserListPage(para).then((res) => {
            // 	this.total = res.data.total;
            // 	this.users = res.data.users;
            // 	this.listLoading = false;
            // 	NProgress.done();
            // });
            request.get(config.api.fund.getShoprecord,para)
                .then((res) => {
                    this.listLoading = false;
                    NProgress.done();
                    let { message, code, data } = res;
                    if (code !== 200) {
                        this.$notify({
                            title: '',
                            message: message,
                            type: 'error'
                        });
                    } else {
                        this.total = data.cnt.total;
                        this.users = data.cnt.data;
                        this.pagesize = data.cnt.per_page || 10;
                    }
                })
        },
        //
        handleDel: function (row) {
            //console.log(row);
            var _this = this;
            this.$confirm('?', '', {
                //type: 'warning'
            }).then(() => {
                _this.listLoading = true;
                NProgress.start();
                let para = { id: row.id };
                removeUser(para).then((res) => {
                    _this.listLoading = false;
                    NProgress.done();
                    _this.$notify({
                        title: '',
                        message: '',
                        type: 'success'
                    });
                    _this.getUsers();
                });

            }).catch(() => {

            });
        },
        showshopdetail(row) {
            this.shopdetailVisible = true;
            request.get(config.api.shop.shopqueuedetail, { shop_id: row.id })
                .then((res) => {
                    let { message, code, data } = res;
                    if (code !== 200) {
                        this.$notify({
                            title: '',
                            message: message,
                            type: 'error'
                        });
                    } else {
                        this.shopdetailobj = data.cnt;
                    }
                })
        },
        //
        showorder(row) {
            this.$router.push('/shoporder');
        },
        //
        handleEdit: function (row) {
            console.log(row)
            this.editFormVisible = true;
            this.editFormTtile = '';
            this.editForm.id = row.id;
            this.editForm.name = row.name;
            this.editForm.logo = row.logo;
            this.filelist = [{ name: 'editlogo', url: row.logo }]
        },
        // or 
        editSubmit: function () {
            var _this = this;

            _this.$refs.editForm.validate((valid) => {
                if (valid) {

                    _this.$confirm('', '', {}).then(() => {
                        _this.editLoading = true;
                        NProgress.start();
                        _this.btnEditText = '';

                        if (_this.editForm.id == 0) {
                            //
                            let para = {
                                name: _this.editForm.name,
                                logo: _this.editForm.logo,
                            };
                            request.post(config.api.help.addbank, para)
                                .then(res => {
                                    let { message, code, data } = res;
                                    _this.editLoading = false;
                                    NProgress.done();
                                    _this.btnEditText = ' ';
                                    if (code !== 200) {
                                        this.$notify({
                                            title: '',
                                            message: message,
                                            type: 'error'
                                        });
                                    } else {
                                        _this.$notify({
                                            title: '',
                                            message: '',
                                            type: 'success'
                                        });
                                        _this.editFormVisible = false;
                                        _this.getUsers();
                                    }
                                })
                        } else {
                            //
                            let para = {
                                id: _this.editForm.id,
                                name: _this.editForm.name,
                                logo: _this.editForm.logo,

                            };
                            request.post(config.api.help.updatebank, para)
                                .then(res => {
                                    let { message, code, data } = res;
                                    _this.editLoading = false;
                                    NProgress.done();
                                    _this.btnEditText = ' ';
                                    if (code !== 200) {
                                        this.$notify({
                                            title: '',
                                            message: message,
                                            type: 'error'
                                        });
                                    } else {
                                        _this.$notify({
                                            title: '',
                                            message: '',
                                            type: 'success'
                                        });
                                        _this.editFormVisible = false;
                                        _this.getUsers();
                                    }
                                })

                        }

                    });

                }
            });

        },
        //
        handleAdd: function () {
            var _this = this;

            this.editFormVisible = true;
            this.editFormTtile = '';

            this.editForm.id = 0;
            this.editForm.name = '';
            this.editForm.logo = '';
        },
        //
        updatestatus(row) {
            let _this=this;
            this.editstatusLoading=true;
            let para = {
                id:row.id,
                status:row.status==0?1:0
            };
            request.post(config.api.help.updatestatus, para)
                .then(res => {
                    let { message, code, data } = res;
                    _this.editstatusLoading = false;
                    NProgress.done();
                    _this.btnEditText = ' ';
                    if (code !== 200) {
                        this.$notify({
                            title: '',
                            message: message,
                            type: 'error'
                        });
                    } else {
                        _this.$notify({
                            title: '',
                            message: '',
                            type: 'success'
                        });
                        _this.getUsers();
                    }
                })
        }
    },
    mounted() {
        this.getUsers();
    }
}
</script>

<style scoped>

</style>


