<template>
    <section>
        <!---->
        <el-col :span="24" class="toolbar">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.tel" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-input v-model="filters.order_sn" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-col :span="11">
                        <el-date-picker type="date" placeholder="" v-model="filters.start_time" style="width: 100%;"></el-date-picker>
                    </el-col>
                    <el-col class="line" :span="2"> - </el-col>
                    <el-col :span="11">
                        <el-time-picker type="fixed-time" placeholder="" v-model="filters.end_time" style="width: 100%;"></el-time-picker>
                    </el-col>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getUsers"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
    
        <el-col :span="24" class="toolbar">
            <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
                <el-tab-pane label="" name="10"></el-tab-pane>
                <el-tab-pane label="" name="1"></el-tab-pane>
                <el-tab-pane label="" name="2"></el-tab-pane>
                <el-tab-pane label="" name="0"></el-tab-pane>
            </el-tabs>
        </el-col>
        <!---->
        <template>
            <el-table :data="users" highlight-current-row v-loading="listLoading" style="width: 100%;">
                <el-table-column type="index" width="60">
                </el-table-column>
                <el-table-column prop="order_sn" label="" width="180" sortable>
                </el-table-column>
                <el-table-column prop="tel" label="" width="180" sortable>
                </el-table-column>
                <el-table-column prop="created_at" label="" width="180" sortable>
                </el-table-column>
                <el-table-column prop="pay_amount" label="" width="120" sortable>
                </el-table-column>
                <el-table-column prop="status" label="" width="150" :formatter="formatSex" sortable>
                </el-table-column>
                <el-table-column :context="_self" label="" min-width="200">
                    <template scope="scope">
                        <el-button  size="small" v-if='scope.row.status==1' @click="audit(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </template>
    
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom:10px;">
            <el-pagination layout="total,sizes,prev, pager, next" :current-page="filters.page" @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-sizes="[10,15, 20, 300, 400]" :page-size="pagesize" :total="total" style="float:right;">
            </el-pagination>
        </el-col>
    
        <!---->
        <el-dialog :title="editFormTtile" v-model="editFormVisible" :close-on-click-modal="false">
            <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
                <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-switch on-text="" off-text="" v-model="editForm.delivery"></el-switch>
                </el-form-item>
                <el-form-item label="">
                    <el-switch on-text="" off-text="" v-model="editForm.delivery"></el-switch>
                </el-form-item>
                <el-form-item label="" prop="ceshi">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="textarea" v-model="editForm.addr"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editFormVisible = false"> </el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
            </div>
        </el-dialog>
    </section>
</template>

<script>
import util from '../../common/util'
import NProgress from 'nprogress'
import request from 'api';
import config from 'config';

export default {
    data() {
        return {
            activeName: '10',
            filters: {
                tel: '',
                status: '',
                order_sn: '',
                start_time: '',
                end_time: '',
                page:1
            },
            users: [],
            total: 0,
            page: 1,
            pagesize: 10,
            listLoading: false,
            editFormVisible: false,//
            editFormTtile: '',//
            //
            editForm: {
                id: 0,
                name: '',
                sex: -1,
                age: 0,
                birth: '',
                addr: ''
            },
            editLoading: false,
            sendLoading: false,
            btnEditText: ' ',
            editFormRules: {
                name: [
                    { required: true, message: '', trigger: 'blur' }
                ]
            }

        }
    },
    methods: {
        //
        formatSex(row, column) {
            return row.status == 1 ? '' : row.status == 0 ? '' : '';
        },
        handleClick(tab, event) {
            this.filters.page = 1;
            if (tab.name == 10) {
                this.filters.status = '';
            } else {
                this.filters.status = tab.name
            }
            console.log(tab, event)
            this.getUsers();
        },

        handleCurrentChange(val) {
            if (this.filters.page != val) {
                this.page = val;
                this.filters.page = val;
                this.getUsers();
            }

        },
        handleSizeChange(val) {
            console.log(` ${val} `);
        },
        //
        getUsers() {
            let para = {
                page: this.page,
                name: this.filters.name
            };
            this.listLoading = true;
            NProgress.start();
            request.get(config.api.order.index, this.filters)
                .then((res) => {
                    this.listLoading = false;
                    NProgress.done();
                    let { message, code, data } = res;
                    if (code !== 200) {
                        this.$notify({
                            title: '',
                            message: message,
                            type: 'error'
                        });
                    } else {
                        this.total = data.cnt.total;
                        this.users = data.cnt.data;
                        this.pagesize = data.cnt.per_page || 10;
                    }
                })

        },
        //
        handleDel: function (row) {
            //console.log(row);
            var _this = this;
            this.$confirm('?', '', {
                //type: 'warning'
            }).then(() => {
                _this.listLoading = true;
                NProgress.start();
                let para = { id: row.id };
                removeUser(para).then((res) => {
                    _this.listLoading = false;
                    NProgress.done();
                    _this.$notify({
                        title: '',
                        message: '',
                        type: 'success'
                    });
                    _this.getUsers();
                });

            }).catch(() => {

            });
        },
        //
        handleEdit: function (row) {
            this.editFormVisible = true;
            this.editFormTtile = '';
            this.editForm.id = row.id;
            this.editForm.name = row.name;
            this.editForm.sex = row.sex;
            this.editForm.age = row.age;
            this.editForm.birth = row.birth;
            this.editForm.addr = row.addr;
        },
        audit(row) {
            this.$confirm('', '', {})
                .then(() => {
                    this.listLoading = true;
                    NProgress.start();
                    request.post(config.api.order.send, { id: row.id })
                        .then((res) => {
                            this.listLoading = false;
                            NProgress.done();
                            let { message, code, data } = res;
                            if (code !== 200) {
                                this.$notify({
                                    title: '',
                                    message: message,
                                    type: 'error'
                                });
                            } else {
                                this.$notify({
                                    title: '',
                                    message: message,
                                    type: 'success'
                                });
                               
                                this.getUsers();
                            }
                        })
                })
        },
        // or 
        editSubmit: function () {
            var _this = this;

            _this.$refs.editForm.validate((valid) => {
                if (valid) {

                    _this.$confirm('', '', {}).then(() => {
                        _this.editLoading = true;
                        NProgress.start();
                        _this.btnEditText = '';

                        if (_this.editForm.id == 0) {
                            //
                            let para = {
                                name: _this.editForm.name,
                                sex: _this.editForm.sex,
                                age: _this.editForm.age,
                                birth: _this.editForm.birth == '' ? '' : util.formatDate.format(new Date(_this.editForm.birth), 'yyyy-MM-dd'),
                                addr: _this.editForm.addr,
                            };
                            addUser(para).then((res) => {
                                _this.editLoading = false;
                                NProgress.done();
                                _this.btnEditText = ' ';
                                _this.$notify({
                                    title: '',
                                    message: '',
                                    type: 'success'
                                });
                                _this.editFormVisible = false;
                                _this.getUsers();
                            });
                        } else {
                            //
                            let para = {
                                id: _this.editForm.id,
                                name: _this.editForm.name,
                                sex: _this.editForm.sex,
                                age: _this.editForm.age,
                                birth: _this.editForm.birth == '' ? '' : util.formatDate.format(new Date(_this.editForm.birth), 'yyyy-MM-dd'),
                                addr: _this.editForm.addr,
                            };
                            editUser(para).then((res) => {
                                _this.editLoading = false;
                                NProgress.done();
                                _this.btnEditText = ' ';
                                _this.$notify({
                                    title: '',
                                    message: '',
                                    type: 'success'
                                });
                                _this.editFormVisible = false;
                                _this.getUsers();
                            });

                        }

                    });

                }
            });

        },
        //
        handleAdd: function () {
            var _this = this;

            this.editFormVisible = true;
            this.editFormTtile = '';

            this.editForm.id = 0;
            this.editForm.name = '';
            this.editForm.sex = 1;
            this.editForm.age = 0;
            this.editForm.birth = '';
            this.editForm.addr = '';
        }
    },
    mounted() {
        this.getUsers();
    }
}
</script>

<style scoped>

</style>


