<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.code" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUsers"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
	
		<el-col :span="24" class="toolbar">
			<el-tabs v-model="activeName" type="card" @tab-click="handleClick">
				<el-tab-pane label="" name="10"></el-tab-pane>
				<el-tab-pane label="" name="0"></el-tab-pane>
				<el-tab-pane label="" name="1"></el-tab-pane>
			</el-tabs>
		</el-col>
	
		<!---->
		<template>
			<el-table ref="multipleTable" @selection-change="handleSelectionChange" :data="users" highlight-current-row v-loading="listLoading" style="width: 100%;">
				<el-table-column type="index" width="60">
				</el-table-column>
				<el-table-column prop="name" label="" width="120" sortable>
				</el-table-column>
				<el-table-column prop="code" label="" width="150" sortable>
				</el-table-column>
				<el-table-column prop="supplier.name" label="" width="180" sortable>
				</el-table-column>
				<el-table-column prop="brand.name" label="" width="120" sortable>
				</el-table-column>
				<!--<el-table-column prop="norm" label="" width="120" sortable>
				</el-table-column>-->
				<el-table-column prop="buying_price" label="" width="100" sortable>
				</el-table-column>
				<el-table-column prop="retail_price" label="" width="100" sortable>
				</el-table-column>
				<el-table-column prop="status" label="" width="120" :formatter="formatSex" sortable>
				</el-table-column>
				<el-table-column inline-template :context="_self" label="" min-width="200">
					<span>
						<el-button size="small" @click="handleEdit(row)"></el-button>
						<!--<el-button type="danger" size="small" @click="handleDel(row)"></el-button>-->
					</span>
				</el-table-column>
			</el-table>
		</template>
	
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom:10px;">
			<el-pagination layout="total,sizes,prev, pager, next" :current-page="filters.page" @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-sizes="[10, 20, 300, 400]" :page-size="pagesize" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	
		<!---->
		<el-dialog :title="editFormTtile" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
				<el-card class="box-card" style="margin-bottom:20px">
					<div slot="header" class="clearfix">
						<span style="line-height: 20px;"></span>
					</div>
					<el-form-item label="" prop="name" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
						<el-input v-model="editForm.name" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" prop="code" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
						<el-input v-model="editForm.code" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" prop="norm" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
						<el-input v-model="editForm.norm" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" prop="production_origin" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
						<el-input v-model="editForm.production_origin" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" prop="manufacturer" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
						<el-input v-model="editForm.manufacturer" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
						<el-select v-model="editForm.valuation_id" placeholder="">
							<el-option v-for="(item,index) in preData.valuation" :key="index" :label="item" :value="index"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
						<el-select v-model="editForm.retail_unit_id" placeholder="">
							<el-option v-for="(item,index) in preData.retail_nuit" :key="index" :label="item" :value="index"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
						<!--<el-select v-model="editForm.goods_type_id" placeholder="">
								
								
							</el-select>-->
						<el-cascader @change="changegoodstype" :options="options" :show-all-levels="false"></el-cascader>
					</el-form-item>
					<el-form-item label="" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
						<el-select v-model="editForm.goods_brand_id" placeholder="">
							<el-option v-for="(item,index) in preData.brand" :key="index" :label="item.name" :value="item.id"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
						<el-select v-model="editForm.supplier_id" placeholder="">
							<el-option v-for="(item,index) in preData.supplier" :key="index" :label="item.name" :value="item.id"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
						<el-switch v-model="editForm.is_rebate" on-color="#13ce66" off-color="#ff4949" on-value="1" off-value="0">
						</el-switch>
					</el-form-item>
					<el-form-item label="" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
						<el-input v-model="editForm.num" auto-complete="off"></el-input>
					</el-form-item>
	
				</el-card>
				<el-card class="box-card">
					<div slot="header" class="clearfix">
						<span style="line-height: 20px;"></span>
					</div>
					<el-form-item label="" prop="buying_price" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
						<el-input v-model="editForm.buying_price" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" prop="trade_price" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
						<el-input v-model="editForm.trade_price" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" prop="retail_price" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
						<el-input v-model="editForm.retail_price" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" prop="market_price" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
						<el-input v-model="editForm.market_price" auto-complete="off"></el-input>
					</el-form-item>
					<!--<el-form-item label="" prop="name" style="display:inline-block;margin:10px;width:30%;min-width:200px;">
							<el-input v-model="editForm.name" auto-complete="off"></el-input>
						</el-form-item>-->
				</el-card>
	
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"> </el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
import util from '../../common/util'
import NProgress from 'nprogress'
import request, { getUserListPage, removeUser, editUser, addUser } from 'api';
import config from 'config';

export default {
	data() {
		return {
			filters: {
				name: '',
				code: '',
				page: 1,
				is_edit: ''
			},
			preData: {
				brand: [],
				retail_nuit: {},
				supplier: [],
				valuation: {},
				type: []
			},
			activeName: '10',
			options: [{
				value: "1",
				label: 'ceshi'
			}],
			users: [],
			total: 0,
			page: 1,
			pagesize: 10,
			listLoading: false,
			editFormVisible: false,//
			editFormTtile: '',//
			shopdetailVisible: false,
			shopdetailtitle: '',
			shopdetailobj: {

			},
			pick: ' 0',
			notpick: ' 0',
			//
			editForm: {
				id: 0,
				name: '',
				code: '',
				production_origin: '',
				manufacturer: '',
				valuation_id: '',
				retail_unit_id: '',
				goods_type_id: '',
				goods_brand_id: '',
				supplier_id: '',
				buying_price: '',
				trade_price: '',
				retail_price: '',
				num: '',
				is_rebate: '1',
				norm: '',
				market_price: ''
			},
			editLoading: false,
			btnEditText: ' ',
			editFormRules: {
				name: [
					{ required: true, message: '', trigger: 'blur' }
				]
			},
			multipleSelection: []
		}
	},
	methods: {
		//
		changegoodstype(value) {
			this.editForm.goods_type_id = value[value.length - 1]
		},
		//
		toggleSelection(rows) {
			if (rows) {
				rows.forEach(row => {
					this.$refs.multipleTable.toggleRowSelection(row, true);
				});
			} else {
				this.$refs.multipleTable.clearSelection();
			}
		},
		handleSelectionChange(val) {
			this.multipleSelection = val;
		},
		//
		formatSex: function (row, column) {
			return row.is_sell == 1 ? '' : row.is_sell == 0 ? '' : '';
		},
		handleClick(tab, event) {
			this.filters.page = 1;
			if (tab.name == 10) {
				this.filters.is_edit = '';
			} else {
				this.filters.is_edit = tab.name;
			}

			this.getUsers();
		},
		handleCurrentChange(val) {
			if (this.filters.page != val) {
				this.filters.page = val;
				this.getUsers();
			}
		},
		handleSizeChange(val) {
			console.log(` ${val} `);
		},
		getPreAdd() {
			request.get(config.api.goods.preparams)
				.then(res => {
					console.log(res)
					let { message, code, data } = res;
					if (code !== 200) {
						this.$notify({
							title: '',
							message: message,
							type: 'error'
						});
					} else {
						this.preData = data.cnt;
						this.preData.type.forEach(item => {
							let obj = { value: item.id, label: item.name, children: [] };
							this.options.push(obj);
							item.sub_type.forEach(its => {
								obj.children.push({ value: its.id, label: its.name })
							})
						})

					}
				})
		},
		//
		getUsers() {

			let para = {
				page: this.page,
				code: this.filters.code,
				// type_id:this.filters.type_id,
				name: this.filters.name
			};
			this.listLoading = true;
			NProgress.start();
			// getUserListPage(para).then((res) => {
			// 	this.total = res.data.total;
			// 	this.users = res.data.users;
			// 	this.listLoading = false;
			// 	NProgress.done();
			// });
			request.get(config.api.goods.index, this.filters)
				.then((res) => {
					this.listLoading = false;
					NProgress.done();
					let { message, code, data } = res;
					if (code !== 200) {
						this.$notify({
							title: '',
							message: message,
							type: 'error'
						});
					} else {

						this.total = data.cnt.good.total;
						this.users = data.cnt.good.data;
						data.cnt.num.forEach((item) => {
							if (item.status == 0) {
								this.notpick = ' ' + item.num
							} else {
								this.pick = ' ' + item.num
							}
						})
						this.pagesize = data.cnt.good.per_page || 10;
					}
				})
		},
		//
		handleDel: function (row) {
			//console.log(row);
			var _this = this;
			this.$confirm('?', '', {
				//type: 'warning'
			}).then(() => {
				_this.listLoading = true;
				NProgress.start();
				let para = { id: row.id };
				removeUser(para).then((res) => {
					_this.listLoading = false;
					NProgress.done();
					_this.$notify({
						title: '',
						message: '',
						type: 'success'
					});
					_this.getUsers();
				});

			}).catch(() => {

			});
		},
		showshopdetail(row) {
			this.shopdetailVisible = true;
			request.get(config.api.shop.shopqueuedetail, { shop_id: row.id })
				.then((res) => {
					let { message, code, data } = res;
					if (code !== 200) {
						this.$notify({
							title: '',
							message: message,
							type: 'error'
						});
					} else {
						this.shopdetailobj = data.cnt;
					}
				})
		},
		//
		showorder(row) {
			this.$router.push('/shoporder');
		},
		//
		handleEdit: function (row) {
			this.editFormVisible = true;
			this.editFormTtile = '';
			this.editForm = row;
			this.$router.push('/goods-detail-edit/' + row.id);
		},
		// or 
		editSubmit: function () {
			var _this = this;

			_this.$refs.editForm.validate((valid) => {
				if (valid) {

					_this.$confirm('', '', {}).then(() => {
						_this.editLoading = true;
						NProgress.start();
						_this.btnEditText = '';

						if (_this.editForm.id == 0) {
							//
							let para = _this.editForm;
							delete para.id;
							request.post(config.api.goods.addGoods, para)
								.then(res => {
									let { message, code, data } = res;
									_this.editLoading = false;
									NProgress.done();
									_this.btnEditText = ' ';
									if (code !== 200) {
										this.$notify({
											title: '',
											message: message,
											type: 'error'
										});
									} else {
										_this.$notify({
											title: '',
											message: '',
											type: 'success'
										});
										_this.editFormVisible = false;
										_this.getUsers();
									}
								})
						} else {
							//
							let para = _this.editForm;
							request.post(config.api.goods.updateGoods, para)
								.then(res => {
									let { message, code, data } = res;
									_this.editLoading = false;
									NProgress.done();
									_this.btnEditText = ' ';
									if (code !== 200) {
										this.$notify({
											title: '',
											message: message,
											type: 'error'
										});
									} else {
										_this.$notify({
											title: '',
											message: '',
											type: 'success'
										});
										_this.editFormVisible = false;
										_this.getUsers();
									}
								})

						}

					});

				}
			});

		},
		//
		handleAdd: function () {
			var _this = this;

			this.editFormVisible = true;
			this.editFormTtile = '';
			for (let key in this.editForm) {
				this.editForm[key] = '';
			}
			this.editForm.id = 0;

			// this.$router.push('/addshop');
		},
		//
		toggleGoodsStatus(sta) {
			sta = sta == '0' ? 1 : 0;
			let arr = [];
			this.multipleSelection.forEach(data => {
				arr.push(data.id);
			})
			let para = {
				sell: sta,
				ids: arr.join(',')
			}
			this.toogleLoading = true;
			NProgress.start();
			request.post(config.api.goods.goodstooglestatus, para)
				.then(res => {
					this.toogleLoading = false;
					NProgress.done();
					let { message, code, data } = res;
					if (code !== 200) {
						this.$notify({
							title: '',
							message: message,
							type: 'error'
						});
					} else {
						console.log(res)
						this.$notify({
							title: '',
							message: message,
							type: 'success'
						});
						this.getUsers();
					}
				})

		}
	},
	mounted() {
		this.getUsers();
		this.getPreAdd()
	}
}
</script>

<style scoped>
.el-card__header {
	background-color: #E4E4E4
}

.clearfix:before,
.clearfix:after {
	display: table;
	content: "";
}

.clearfix:after {
	clear: both
}
</style>


