<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.real_name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.user_mobile" placeholder=""></el-input>
				</el-form-item>

				<el-form-item>
					<el-button type="primary" v-on:click="getUsers"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
	
		<!---->
		<template>
			<el-table :data="users" highlight-current-row v-loading="listLoading" style="width: 100%;">
				<el-table-column type="index" width="60">
				</el-table-column>
				<el-table-column prop="user_mobile" label="" width="150" sortable>
				</el-table-column>
				<el-table-column prop="user_nickname" label="" width="100" sortable>
				</el-table-column>
				<el-table-column prop="real_name" label="" width="100" sortable>
				</el-table-column>
				<el-table-column prop="id_card" label="" width="180" sortable>
				</el-table-column>
				<el-table-column prop="created_at" label="" width="180" sortable>
				</el-table-column>
				<el-table-column prop="shop_name" label="" width="150" sortable>
				</el-table-column>
				<el-table-column prop="balance" label="" width="150" sortable>
				</el-table-column>
				<el-table-column inline-template :context="_self" label="" min-width="200">
					<span>
						<el-button size="small" @click="getUserLog(row)"></el-button>
						<el-button size="small" @click="handleEdit(row)"></el-button>
					</span>
				</el-table-column>
			</el-table>
		</template>
	
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom:10px;">
			<el-pagination layout="total,sizes,prev, pager, next" @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-sizes="[10, 200, 300, 400]" :page-size="pagesize" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	
	
		<!---->
		<el-dialog :title="editFormTtile" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="user_mobile">
					<el-input v-model="editForm.user_mobile" :disabled="true" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="user_nickname">
					<el-input v-model="editForm.user_nickname" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-switch on-text="" off-text="" on-value='1' off-value='0' v-model="editForm.status"></el-switch>
				</el-form-item>
				<el-form-item label="">
					<el-switch on-text="" off-text="" on-value='1' off-value='0' v-model="editForm.into_queque"></el-switch>
				</el-form-item>
				<el-form-item label="" prop="user_pwd">
					<el-input v-model="editForm.user_pwd" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="real_name">
					<el-input v-model="editForm.real_name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="id_card">
					<el-input v-model="editForm.id_card" auto-complete="off"></el-input>
				</el-form-item>
				<!--<el-form-item label="" prop="">
					<el-input v-model="editForm.bank" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="">
					<el-input v-model="editForm.card" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" v-model="editForm.addr"></el-input>
				</el-form-item>-->
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"> </el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
import util from '../../common/util'
import NProgress from 'nprogress'
import request from 'api';
import config from 'config';

export default {
	data() {
		return {
			filters: {
				real_name: '',
				user_mobile:'',
				page:1
			},
			users: [],
			total: 0,
			page: 1,
			pagesize: 10,
			listLoading: false,
			editFormVisible: false,//
			editFormTtile: '',//
			//
			editForm: {
				id: 0,
				status:'1',
				into_queque:'1'
			},
			editLoading: false,
			btnEditText: ' ',
			editFormRules: {
				name: [
					{ required: true, message: '', trigger: 'blur' }
				]
			}

		}
	},
	methods: {
		//
		formatSex: function (row, column) {
			return row.sex == 1 ? '' : row.sex == 0 ? '' : '';
		},
		handleCurrentChange(val) {
			this.page = val;
			this.filters.page = val;
			this.getUsers();
		},
		handleSizeChange(val) {
			console.log(` ${val} `);
		},
		//
		getUsers() {
			let para = {
				page: this.page,
				name: this.filters.name
			};
			this.listLoading = true;
			NProgress.start();
			request.get(config.api.vip.index,this.filters)
				.then((res) => {
					this.listLoading = false;
					NProgress.done();
					let { message, code, data } = res;
					if (code !== 200) {
						this.$notify({
							title: '',
							message: message,
							type: 'error'
						});
					} else {
						this.total = data.cnt.total;
						this.users = data.cnt.data;
						this.pagesize = data.cnt.per_page || 10;
					}
				})

		},
		//
		handleDel: function (row) {
			//console.log(row);
			var _this = this;
			this.$confirm('?', '', {
				//type: 'warning'
			}).then(() => {
				_this.listLoading = true;
				NProgress.start();
				let para = { id: row.id };
				removeUser(para).then((res) => {
					_this.listLoading = false;
					NProgress.done();
					_this.$notify({
						title: '',
						message: '',
						type: 'success'
					});
					_this.getUsers();
				});

			}).catch(() => {

			});
		},
		//
		handleEdit: function (row) {
			this.editFormVisible = true;
			this.editFormTtile = '';
			this.editForm=row;
			this.editForm.status+='';
			this.editForm.into_queque+='';
			this.editForm.user_pwd='';
		},
		//
		getUserLog(row){
			this.$router.push('/viplog/'+row.id);
		},
		// or 
		editSubmit: function () {
			var _this = this;

			_this.$refs.editForm.validate((valid) => {
				if (valid) {

					_this.$confirm('', '', {}).then(() => {
						_this.editLoading = true;
						NProgress.start();
						_this.btnEditText = '';

						if (_this.editForm.id == 0) {
							//
							let para = _this.editForm;
							delete para.id;
							request.post(config.api.vip.update, para)
                                .then(res => {
                                    let { message, code, data } = res;
                                    _this.editLoading = false;
                                    NProgress.done();
                                    _this.btnEditText = ' ';
                                    if (code !== 200) {
                                        this.$notify({
                                            title: '',
                                            message: message,
                                            type: 'error'
                                        });
                                    } else {
                                        _this.$notify({
                                            title: '',
                                            message: '',
                                            type: 'success'
                                        });
                                        _this.editFormVisible = false;
                                        _this.getUsers();
                                    }
                                })
						} else {
							//
							let para = _this.editForm;
							request.post(config.api.vip.update, para)
                                .then(res => {
                                    let { message, code, data } = res;
                                    _this.editLoading = false;
                                    NProgress.done();
                                    _this.btnEditText = ' ';
                                    if (code !== 200) {
                                        this.$notify({
                                            title: '',
                                            message: message,
                                            type: 'error'
                                        });
                                    } else {
                                        _this.$notify({
                                            title: '',
                                            message: '',
                                            type: 'success'
                                        });
                                        _this.editFormVisible = false;
                                        _this.getUsers();
                                    }
                                })

						}

					});

				}
			});

		},
		//
		handleAdd: function () {
			var _this = this;

			this.editFormVisible = true;
			this.editFormTtile = '';

			this.editForm.id = 0;
			this.editForm.name = '';
			this.editForm.sex = 1;
			this.editForm.age = 0;
			this.editForm.birth = '';
			this.editForm.addr = '';
		}
	},
	mounted() {
		this.getUsers();
	}
}
</script>

<style scoped>

</style>


