<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.shopname" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUsers"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
	
		<!---->
		<template>
			<el-table :data="users" highlight-current-row v-loading="listLoading" style="width: 100%;">
				<el-table-column type="index" width="60">
				</el-table-column>
				<el-table-column prop="login" label="" width="120" sortable>
				</el-table-column>
				<el-table-column prop="shopname" label="" width="120" sortable>
				</el-table-column>
				<el-table-column prop="shopuser" label="" width="120" sortable>
				</el-table-column>
				<el-table-column prop="status" label="" :formatter="formatSex" width="120" sortable>
	
				</el-table-column>
				<el-table-column prop="card" label="" width="180" sortable>
				</el-table-column>
				<el-table-column prop="created_at" label="" width="180" sortable>
				</el-table-column>
				<el-table-column inline-template :context="_self" label="" min-width="320">
					<span>
						<el-button size="small" @click="showshopdetail(row)"></el-button>
						<el-button size="small" @click="showorder(row)"></el-button>
						<el-button size="small" @click="getShopLog(row)"></el-button>
						<el-button size="small" @click="handleEdit(row)"></el-button>
						<el-button type="danger" size="small" @click="handleDel(row)"></el-button>
					</span>
				</el-table-column>
			</el-table>
		</template>
	
		<!---->
        <el-col :span="24" class="toolbar" style="padding-bottom:10px;">
            <el-pagination layout="total,sizes,prev, pager, next" @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-sizes="[10, 200, 300, 400]" :page-size="pagesize" :total="total" style="float:right;">
            </el-pagination>
        </el-col>
	
		<!---->
		<el-dialog :title="editFormTtile" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<!--<el-select v-model="editForm.sex" placeholder="">
										<el-option label="" :value="1"></el-option>
										<el-option label="" :value="0"></el-option>
									</el-select>-->
					<el-radio-group v-model="editForm.sex">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="">
					<el-input-number v-model="editForm.age" :min="0" :max="200"></el-input-number>
				</el-form-item>
				<el-form-item label="">
					<el-date-picker type="date" placeholder="" v-model="editForm.birth"></el-date-picker>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" v-model="editForm.addr"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"> </el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
			</div>
		</el-dialog>
		<!---->
		<el-dialog :title="shopdetailtitle" v-model="shopdetailVisible" :close-on-click-modal="false">
			<el-form :model="shopdetailobj" label-width="80px">
				<el-form-item label="" prop="name">
					<el-input v-model="shopdetailobj.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="name">
					<el-input v-model="shopdetailobj.min_amount" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="name">
					<el-input v-model="shopdetailobj.max_amount" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="name">
					<el-input v-model="shopdetailobj.spec" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="name">
					<el-input v-model="shopdetailobj.spec" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="name">
					<el-input type="textarea" v-model="shopdetailobj.desc"></el-input>
				</el-form-item>
			</el-form>
	
		</el-dialog>
	</section>
</template>

<script>
import util from '../../common/util'
import NProgress from 'nprogress'
import request, { getUserListPage, removeUser, editUser, addUser } from 'api';
import config from 'config';

export default {
	data() {
		return {
			filters: {
				shopname: '',
				page:1
			},
			users: [],
			total: 0,
			page: 1,
			pagesize: 10,
			listLoading: false,
			editFormVisible: false,//
			editFormTtile: '',//
			shopdetailVisible: false,
			shopdetailtitle: '',
			shopdetailobj: {

			},
			//
			editForm: {
				id: 0,
				name: '',
				
			},
			editLoading: false,
			btnEditText: ' ',
			editFormRules: {
				name: [
					{ required: true, message: '', trigger: 'blur' }
				]
			}

		}
	},
	methods: {
		//
		formatSex: function (row, column) {
			return row.status == 1 ? '' : row.status == 0 ? '' : '';
		},
		handleCurrentChange(val) {
			this.page = val;
			this.filters.page = val;
			this.getUsers();
		},
		handleSizeChange(val) {
			console.log(` ${val} `);
		},
		//
		getUsers() {
			let para = {
				page: this.page,
				name: this.filters.name
			};
			this.listLoading = true;
			NProgress.start();
			// getUserListPage(para).then((res) => {
			// 	this.total = res.data.total;
			// 	this.users = res.data.users;
			// 	this.listLoading = false;
			// 	NProgress.done();
			// });
			console.log(request);
			request.get(config.api.shop.showlist,this.filters)
				.then((res) => {
					this.listLoading = false;
					NProgress.done();
					let { message, code, data } = res;
					if (code !== 200) {
						this.$notify({
							title: '',
							message: message,
							type: 'error'
						});
					} else {
						this.total = data.cnt.total;
						this.users = data.cnt.data;
						this.pagesize = data.cnt.per_page || 10;
					}
				})
		},
		//
		handleDel: function (row) {
			//console.log(row);
			var _this = this;
			this.$confirm('?', '', {
				//type: 'warning'
			}).then(() => {
				_this.listLoading = true;
				NProgress.start();
				let para = { id: row.id };
				request.post(config.api.shop.delete,para)
					.then((res) => {
						_this.listLoading = false;
						NProgress.done();
						let { message, code, data } = res;
						if (code !== 200) {
							_this.$notify({
								title: '',
								message: message,
								type: 'error'
							});
						} else {
							_this.$notify({
								title: '',
								message: message,
								type: 'sucess'
							});
							_this.getUsers();
						}
					})

			}).catch(() => {

			});
		},
		showshopdetail(row) {
			this.shopdetailVisible = true;
			request.get(config.api.shop.shopqueuedetail, { shop_id: row.id })
				.then((res) => {
					let { message, code, data } = res;
					if (code !== 200) {
						this.$notify({
							title: '',
							message: message,
							type: 'error'
						});
					} else {
						this.shopdetailobj = data.cnt;
					}
				})
		},
		//
		showorder(row) {
			this.$router.push('/shoporder/' + row.id);
		},
		getShopLog(row) {
			this.$router.push('/shoplog/' + row.id);
		},
		//
		handleEdit: function (row) {
			// this.editFormVisible = true;
			// this.editFormTtile = '';
			// this.editForm.id = row.id;
			// this.editForm.name = row.name;
			// this.editForm.sex = row.sex;
			// this.editForm.age = row.age;
			// this.editForm.birth = row.birth;
			// this.editForm.addr = row.addr;
			this.$router.push('/addshop/' + row.id);
		},
		// or 
		editSubmit: function () {
			var _this = this;

			_this.$refs.editForm.validate((valid) => {
				if (valid) {

					_this.$confirm('', '', {}).then(() => {
						_this.editLoading = true;
						NProgress.start();
						_this.btnEditText = '';

						if (_this.editForm.id == 0) {
							//
							let para = {
								name: _this.editForm.name,
								sex: _this.editForm.sex,
								age: _this.editForm.age,
								birth: _this.editForm.birth == '' ? '' : util.formatDate.format(new Date(_this.editForm.birth), 'yyyy-MM-dd'),
								addr: _this.editForm.addr,
							};
							addUser(para).then((res) => {
								_this.editLoading = false;
								NProgress.done();
								_this.btnEditText = ' ';
								_this.$notify({
									title: '',
									message: '',
									type: 'success'
								});
								_this.editFormVisible = false;
								_this.getUsers();
							});
						} else {
							//
							let para = {
								id: _this.editForm.id,
								name: _this.editForm.name,
								sex: _this.editForm.sex,
								age: _this.editForm.age,
								birth: _this.editForm.birth == '' ? '' : util.formatDate.format(new Date(_this.editForm.birth), 'yyyy-MM-dd'),
								addr: _this.editForm.addr,
							};
							editUser(para).then((res) => {
								_this.editLoading = false;
								NProgress.done();
								_this.btnEditText = ' ';
								_this.$notify({
									title: '',
									message: '',
									type: 'success'
								});
								_this.editFormVisible = false;
								_this.getUsers();
							});

						}

					});

				}
			});

		},
		//
		handleAdd: function () {
			// var _this = this;

			// this.editFormVisible = true;
			// this.editFormTtile = '';

			// this.editForm.id = 0;
			// this.editForm.name = '';
			// this.editForm.sex = 1;
			// this.editForm.age = 0;
			// this.editForm.birth = '';
			// this.editForm.addr = '';
			this.$router.push('/addshop/0');
		},
		updatestatus(row) {
			let _this = this;

			let para = {
				id: row.id,
				status: row.status == 0 ? 1 : 0
			};
			request.post('', para)
				.then(res => {
					let { message, code, data } = res;

					NProgress.done();

					if (code !== 200) {
						this.$notify({
							title: '',
							message: message,
							type: 'error'
						});
					} else {
						_this.$notify({
							title: '',
							message: '',
							type: 'success'
						});
						_this.getUsers();
					}
				})
		}
	},
	mounted() {
		this.getUsers();
	}
}
</script>

<style scoped>

</style>


